<template>
  <v-container grid-list-xl>
    <v-layout row wrap>
      <v-flex
        xs12
        sm4
        v-for="(tour, index) in tours"
        :key="index">
        <v-card>
          <v-card-title>
            {{ tour.title }}
          </v-card-title>
          <v-card-text>
            {{ tour.addr1 }}
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    tours:[]
  }),
  methods: {
    retrieveUsers(){
      this.$axios.get("/apitest")
          .then(response=>{
            this.tours = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    }
  },
  mounted(){
    this.retrieveUsers();
  }
}
</script>